nav{
    background: rgba(0, 0, 0,0.3);
    width: -webkit-max-content;
    width: max-content;
    display: block;
    padding: 0.7rem 1.7rem;
    z-index: 2;
    position: fixed;
    left: 50%;
    -webkit-transform: translateX(-50%);
            transform: translateX(-50%);
    bottom: 1rem;
    display: flex;
    grid-gap: 0.8rem;
    gap: 0.8rem;
    border-radius: 3rem;
    -webkit-backdrop-filter: blur(15px);
            backdrop-filter: blur(15px);
}
nav a {
    background: transparent;
    padding: 0.8rem;
    border-radius: 50%;
    display: flex;
    color: var(--orange);
    font-size: 1.1rem;
}
 nav a:hover {
    color: rgb(99, 98, 98) !important;
 }
nav a.active {
    background: var(--orange);
    color: whitesmoke;
}




@media screen and (max-width: 280px) {
    nav{
        padding: 0.4rem 1.2rem;
        grid-gap: 0.1rem;
        gap: 0.1rem;
        
    }
    }
header {
  height: 95vh !important;
  padding: 5rem;
  overflow: hidden;
  background-color: black;
  margin: 1em;
}

.header__container {
  text-align: center;
  height: 100%;
  position: relative;
  background: url(/static/media/header.bb3bb591.avif) center no-repeat;
  background-size: cover;
  padding: 10% 3em;
}

h5,
h1 {
  color: var(--orange);
}

.scroll__down {
  text-decoration: none;
  color: var(--orange);
  border: 1px solid whitesmoke;
  padding: .2em;
}

.scroll__down:hover {
  color: whitesmoke;
}

/*===================== CTA ====================*/
.cta {
  margin-top: 2.5rem;
  display: flex;
  grid-gap: 1.2rem;
  gap: 1.2rem;
  justify-content: center;
}

.cta a:hover {
  color: var(--orange);
  background: none !important;
}

/*===================== Header SOCIAL  ====================*/

.header__socials {
  display: flex;
  flex-direction: column;
  align-items: center;
  grid-gap: 0.8rem;
  gap: 0.8rem;
  position: absolute;
  left: 0;
  bottom: 3rem;
}

.header__socials::after {
  content: "";
  width: 1px;
  height: 2rem;
  background-color: var(--dark);
}

.header__socials a {
  border: 1px solid whitesmoke;
  padding: .2em;

  color: whitesmoke !important;
  /* color : rgb(175, 137, 124) */

}

.header__socials a:hover {
  /* color: whitesmoke !important; */
  color: rgb(175, 137, 124) !important;
  border-color: rgb(175, 137, 124) !important;

}


/*================ SCROLL DOWN ===================*/
.scroll__down {
  position: absolute;
  right: -2.3rem;
  bottom: 5rem;
  -webkit-transform: rotate(90deg);
          transform: rotate(90deg);
  font-weight: 300;
  font-size: 0.9rem;
}

/*============= MEDIA QUERIES (MEDIUM DEVICES) ==============*/

@media screen and (max-width: 1024px) {
  header {
    height: 68vh;
  }

  .header__container{
    padding: 12% 3em;
  }
}

@media screen and (max-width: 773px) {
  header {
    height: 100vh;
    padding: 1.5em;
  }
.header__container{
  padding: 25% 3em;
}
}

@media screen and (max-width: 502px) {
  .cta{flex-direction: column;
  
  }
  .header__socials {
    flex-direction: row;
    bottom: 4rem;
  }
  .scroll__down{
    display: none;
  }
  
}

@media screen and (max-width: 280px) {
header{
margin: .4em;
padding: 1em;

}
.header__container{
  padding: 45% .7em ;
}

